<template>
  <!-- 编辑表单对话框 -->
  <EditForm ref="editFormRef" @refresh="refreshData"/>
  
  <!-- 搜索菜单部分的过渡效果 -->
  <transition name="fade">
    <div class="table-com-search" v-show="showSearch">
      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item label="产品名称">
            <el-input 
              v-model="searchForm.product_name" 
              placeholder="按产品名称搜索" 
              clearable 
            />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="产品编号">
            <el-input 
              v-model="searchForm.product_code" 
              placeholder="按产品编号搜索" 
              clearable 
            />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="支付类型">
            <el-select v-model="searchForm.payment_type_id" placeholder="选择支付类型" clearable filterable>
              <el-option
                v-for="type in paymentTypeList"
                :key="type.id"
                :label="type.product_name"
                :value="type.id"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="状态">
            <el-select v-model="searchForm.status" placeholder="选择状态" clearable>
              <el-option label="全部" :value="''" />
              <el-option label="启用" :value="1" />
              <el-option label="禁用" :value="0" />
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>

      <!-- 搜索按钮和重置按钮 -->
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item>
          <el-button type="primary" @click="handleSearch">搜索</el-button>
          <el-button type="primary" @click="handleReset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </transition>

  <!-- 表头 -->
  <div class="table-header ba-scroll-style">
    <!-- 刷新、添加、删除按钮 -->
    <el-button :icon="Refresh" @click="refreshData" color="#40485b"/>
    <el-button type="primary" :icon="Plus" @click="openEditFrom">添加</el-button>
    <el-button type="danger" :disabled="!hasSelectedRows" @click="handleBatchDelete('/api/v1/admin/product/delete')">删除</el-button>

    <!-- 搜索按钮 -->
    <div class="table-search">
      <el-button-group class="ml-4">
        <el-button :icon="Search" @click="toggleSearch" />
      </el-button-group>
    </div>
  </div>

  <!-- 表格内容 -->
  <div style="width: 100%; overflow-x: auto;" class="table-content">
    <el-table
        :data="data.data"
        border
        stripe
        max-height="600"
        style="width: 100%;" 
        @selection-change="handleSelectionChange">
      <!-- 表格列定义 -->
      <el-table-column type="selection" width="40"/>
      <el-table-column prop="product_name" label="产品名称" align="center" min-width="200"/>
      <el-table-column prop="product_code" label="产品编号" align="center" min-width="100"/>
      <el-table-column prop="payment_type.product_name" label="支付类型" align="center" min-width="150"/>

      <!-- 状态列 -->
      <el-table-column label="状态" align="center" min-width="90">
        <template #default="{ row }">
          <el-switch 
            v-model="row.status" 
            :active-value="1" 
            :inactive-value="0" 
            @change="toggleSwitch('/api/v1/admin/product/switch', row.id)"
          />
        </template>
      </el-table-column>

      <el-table-column prop="created_at" label="创建时间" align="center" min-width="150"/>

      <!-- 操作列 -->
      <el-table-column label="操作" align="center" fixed="right" min-width="200">
        <template #default="scope">
          <el-button size="small" @click="openEditFrom(scope.row)">编辑</el-button>
          <el-button size="small" type="danger" @click="handleDelete('/api/v1/admin/product/delete', scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

  </div>

  <!-- 表格分页 -->
  <div class="table-footer">
    <div class="table-pagination">
      <el-pagination
          v-model:current-page="data.current_page"
          v-model:page-size="data.page_size"
          :page-sizes="[10, 50, 100, 200]"
          :background="true"
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="data.page_total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script lang="ts" setup>
import { Plus, Refresh, Search } from '@element-plus/icons-vue';
import { ref, onMounted, defineAsyncComponent } from 'vue';
import request from '@/utils/request';

// 引入自定义插件和 API
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";

// 导入编辑
const EditForm = defineAsyncComponent(() => import('./edit.vue'));
// 导入编辑页面
const editFormRef = ref(null);
const openEditFrom = (row = {}) => {
  console.log(row);
  if (editFormRef.value) {
    editFormRef.value.open({ ...row }); // 传递 `row` 作为参数
  } else {
    console.error("EditForm 组件未正确挂载！");
  }
};

// 使用表格插件来管理表格的状态、数据等
const {
  handleSelectionChange,
  hasSelectedRows,
  isVisible: showSearch,
  toggleVisibility: toggleSearch,
  loadData,
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams,
  deleteRows,
  toggleSwitch,
  resetSearchParams
} = useTableDataPlugin("/api/v1/admin/product/list");

// 搜索表单数据
const searchForm = ref({
  product_name: '',
  product_code: '',
  payment_type_id: '',
  status: ''
});

// 支付类型列表
const paymentTypeList = ref([]);

// 不再需要获取用户信息和代理商列表

// 获取支付类型列表
const getPaymentTypeList = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/product/payment-type-list',
      method: 'GET'
    });
    if (res.status) {
      paymentTypeList.value = res.data;
    }
  } catch (error) {
    console.error('获取支付类型列表失败:', error);
  }
};

// 处理搜索
const handleSearch = async () => {
  // 将搜索表单数据同步到searchParams
  searchParams.value = { ...searchForm.value };
  await refreshData();
};

// 处理重置
const handleReset = () => {
  searchForm.value = {
    product_name: '',
    product_code: '',
    payment_type_id: '',
    status: ''
  };
  searchParams.value = { ...searchForm.value };
  refreshData();
};

// 在组件挂载时加载数据
onMounted(async () => {
  await getPaymentTypeList();
  loadData();
});

// 删除方法：单条删除
const handleDelete = (api: string, row: any) => {
  deleteRows(api, [row.id]);  // 传递一个 id 数组来删除单条记录
};

// 批量删除
const handleBatchDelete = (api: string) => {
  deleteRows(api);
};

</script>

